"use client";
import { useToast } from "@/hooks/use-toast";
import React, { useState, useEffect, useRef } from "react";
import { Input } from '@/components/ui/input';
import {
    History,
    CheckCircle,
    XCircle,
    Clock as PendingIcon,
    Delete,
    Group, MapPin, University, Flame, Rocket, Stars, ChevronLeft, ChevronRight, Tag, Type, Calendar1, Building, List,
    Calendar,
} from "lucide-react";
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { motion, AnimatePresence } from 'framer-motion';
import EventCard from "@/components/ui/EventCard";
import { events } from '@/constants/events';
import { EventCategory } from "@/constants/type";
import { useDebounce } from 'use-debounce';
import Link from "next/link";
import { Calendar as CalendarComp } from '@/components/ui/calendar';
import { Button } from '@/components/ui/button';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';


const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
        opacity: 1,
        y: 0,
        transition: {
            duration: 0.5,
            ease: 'easeOut',
        },
    },
    hover: {
        scale: 1.02,
        transition: {
            duration: 0.3,
        },
    },
};
// تسميات الفلاتر
const filterLabels = [
    { label: 'All', icon: List, value: 'all' },
    { label: 'Active', icon: Flame, value: 'active' },
    { label: 'Past', icon: History, value: 'past' },
    { label: 'Pending', icon: PendingIcon, value: 'pending' },
    { label: 'Approved', icon: CheckCircle, value: 'approved' },
    { label: 'Rejected', icon: XCircle, value: 'rejected' },
];

const FloatingBlobs = () => (
    <>
        {[...Array(5)].map((_, i) => (
            <motion.div
                key={i}
                initial={{ scale: 0, rotate: 0 }}
                animate={{
                    scale: [0.8, 1.2, 0.8],
                    rotate: [0, 180, 360],
                    x: Math.sin((i * Math.PI) / 2.5) * 100,
                    y: Math.cos((i * Math.PI) / 2.5) * 100,
                }}
                transition={{
                    duration: 10 + i * 2,
                    repeat: Infinity,
                    ease: "easeInOut",
                }}
                className={`absolute w-${40 + i * 20} h-${40 + i * 20} opacity-10 blur-xl
                bg-gradient-to-r ${i % 2 ? 'from-pink-400 to-purple-400' : 'from-cyan-400 to-indigo-400'}
                rounded-full`}
            />
        ))}
    </>
);

const AllEventsPage = ({ className }: React.HTMLAttributes<HTMLDivElement>) => {
    const { toast } = useToast();
    const [event, setEvents] = useState<EventCategory[]>([]);
    const [date, setDate] = useState<Date | undefined>(new Date());
    const [activeFilter, setActiveFilter] = useState<string>('all');
    const [searchQuery, setSearchQuery] = useState('');
    const [debouncedQuery] = useDebounce(searchQuery, 300);
    const [isLoading, setIsLoading] = useState(true);
    const [isCalendarOpen, setIsCalendarOpen] = useState(false);
    const [currentPage, setCurrentPage] = useState(1); // حالة Pagination الجديدة
    const [eventsPerPage] = useState(6); // عدد الأحداث في كل صفحة

    useEffect(() => {
        if (isLoading) return;

        const fetchEvents = async () => {
            try {
                setIsLoading(true);
                const response = await fetch("", {
                    method: "GET",
                })
                console.log("status : ", response.status)
                const data = await response.json();
                setEvents(data);

                if (!response.ok) {
                    toast({
                        variant: "destructive",
                        title: "خطأ!",
                        description: "فشل في تحميل الحجوزات",
                    });
                }
            } catch (err: any) {
                toast({
                    variant: "destructive",
                    title: "خطأ!",
                    description: "حدث خطأ أثناء جلب البيانات ، تعذر الاتصال بالسيرفر حاول مرة أخرى.",
                });
            } finally {
                setIsLoading(false);
            }
        }
        fetchEvents();
    }, [activeFilter]);

    // تصفية الأحداث
    const filteredEvents = (events || []).filter((event) => {
        const matchesSearch = event.title.toLowerCase().includes(debouncedQuery.toLowerCase());
        const matchesFilter =
            activeFilter === 'all' ||
            (activeFilter === 'active' && new Date(event.date) > new Date()) ||
            (activeFilter === 'past' && new Date(event.date) <= new Date()) ||
            (activeFilter === 'pending' && event.status === 'pending') ||
            (activeFilter === 'approved' && event.status === 'approved') ||
            (activeFilter === 'rejected' && event.status === 'rejected');

        return matchesSearch && matchesFilter;
    });

    // حساب الأحداث للصفحة الحالية
    const indexOfLastEvent = currentPage * eventsPerPage;
    const indexOfFirstEvent = indexOfLastEvent - eventsPerPage;
    const currentEvents = filteredEvents.slice(indexOfFirstEvent, indexOfLastEvent);
    const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);


    // تغيير الصفحة
    const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

    // إعادة تعيين الصفحة إلى 1 عند تغيير الفلتر
    useEffect(() => {
        setCurrentPage(1);
    }, [activeFilter]);

    return (
        <main className="bg-white min-h-screen relative overflow-hidden">
            <div className="max-w-6xl mx-auto px-4 py-10 relative">

                {/* Controls */}
                <motion.div
                    className="flex flex-col lg:flex-row gap-4 items-stretch my-15"
                    variants={itemVariants}
                >
                    {/* Search Input */}
                    <div className="relative flex-1 max-w-full">
                        <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                            <Search className="h-5 w-5 text-purple-400" />
                        </div>
                        <Input
                            placeholder="Search events..."
                            className="pl-10 pr-4 py-2 rounded-full border border-purple-300 focus:ring-2 focus:ring-purple-500 focus:border-transparent w-full"
                            value={searchQuery}
                            onChange={(e) => {
                                setSearchQuery(e.target.value);
                                setCurrentPage(1);
                            }}
                        />
                    </div>

                    {/* Filters Tabs */}
                    <div className="flex-1 overflow-x-auto">
                        <Tabs
                            value={activeFilter}
                            onValueChange={(val) => {
                                setActiveFilter(val);
                                setCurrentPage(1);
                            }}
                            className="w-full"
                        >
                            <TabsList className="bg-transparent gap-1 p-1 flex-wrap justify-start w-full">
                                {filterLabels.map(({ label, icon: Icon, value }) => (
                                    <TabsTrigger
                                        value={value}
                                        key={value}
                                        className="data-[state=active]:bg-purple-600 data-[state=active]:text-white rounded-full px-3 py-1 text-xs md:text-sm md:px-4 md:py-2 transition-all whitespace-nowrap"
                                    >
                                        <div className="flex items-center gap-1 md:gap-2">
                                            <Icon className="h-3 w-3 md:h-4 md:w-4" />
                                            {label}
                                        </div>
                                    </TabsTrigger>
                                ))}
                            </TabsList>
                        </Tabs>
                    </div>

                    {/* Calendar Popover */}
                    <Popover open={isCalendarOpen} onOpenChange={setIsCalendarOpen}>
                        <PopoverTrigger asChild>
                            <Button variant="outline" className="rounded-full gap-2">
                                <Calendar className="h-4 w-4" />
                                <span>Select Date</span>
                            </Button>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="end">
                            <CalendarComp
                                mode="single"
                                selected={date}
                                onSelect={(selectedDate) => {
                                    setDate(selectedDate);
                                    setIsCalendarOpen(false);
                                }}
                                initialFocus
                            />
                        </PopoverContent>
                    </Popover>
                </motion.div>
                <motion.div
                    className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8"
                    initial="hidden"
                    animate="visible"
                    variants={{
                        hidden: { opacity: 0 },
                        visible: {
                            opacity: 1,
                            transition: {
                                staggerChildren: 0.1,
                                delayChildren: 0.2
                            }
                        }
                    }}
                >
                    <AnimatePresence>
                        {currentEvents.map((event, index) => (
                            <motion.div
                                key={event.id}
                                variants={{
                                    hidden: { opacity: 0, y: 20 },
                                    visible: { opacity: 1, y: 0 }
                                }}
                                whileHover={{
                                    y: -5,
                                    transition: { type: 'spring', stiffness: 300 }
                                }}
                                className="relative group"
                            >
                                <Link href={`events/${event.id}`}>
                                        <EventCard
                                            event={event}
                                            className="transition-all duration-300 group-hover:shadow-xl
                                                        group-hover:shadow-purple-500/20 group-hover:border-cyan-400/30"
                                        >
                                            {/* محتوى البطاقة */}
                                            <div className="p-6 bg-white shadow-xl rounded-2xl space-y-4 hover:shadow-purple-200 transition-all duration-300">
                                                {/* العنوان */}
                                                <div className="text-center">
                                                    <h3 className="text-2xl font-bold text-purple-700">{event.university || "Yarmouk University"}</h3>
                                                </div>

                                                {/* التصنيف */}
                                                <div className="flex items-center gap-2 text-purple-600">
                                                    <Tag className="w-4 h-4" />
                                                    <span className="text-sm font-medium">{event.category}</span>
                                                </div>

                                                {/* العنوان والقسم والوقت */}
                                                <div className="flex justify-between items-start gap-4">
                                                    <div className="space-y-1">
                                                        <div className="flex items-center gap-2 text-gray-800">
                                                            <Type className="w-4 h-4 text-purple-600" />
                                                            <h4 className="font-semibold text-base">Title: {event.title}</h4>
                                                        </div>
                                                        <p className="text-sm text-gray-500">By {event.department}</p>
                                                    </div>
                                                    <div className="flex flex-col items-end">
                                                        <span className="text-sm text-gray-500">Time</span>
                                                        <span className="text-xl font-bold text-purple-600">{event.time}</span>
                                                    </div>
                                                </div>

                                                {/* الوصف */}
                                                <p className="text-sm text-gray-600 leading-relaxed">{event.description}</p>

                                                {/* التاريخ والموقع */}
                                                <div className="grid grid-cols-2 gap-2 text-sm text-gray-700">
                                                    <div className="flex items-center gap-2">
                                                        <Calendar1 className="w-4 h-4 text-purple-500" />
                                                        <span>{event.date}</span>
                                                    </div>
                                                    <div className="flex items-center gap-2">
                                                        <Building className="w-4 h-4 text-purple-500" />
                                                        <span>Hall: {event.hall}</span>
                                                </div>
                                                {event.status === "approved" ?
                                                    <div className="flex items-center gap-2">
                                                        <University className="w-4 h-4 text-purple-500" />
                                                        <span>Capacity: 10/{event.capacity}</span>
                                                    </div> 
                                                    : event.status === "pending" ? 
                                                        <div className="flex items-center gap-2">
                                                            <University className="w-4 h-4 text-purple-500" />
                                                            <span>Capacity: {event.capacity}</span>
                                                        </div>
                                                    :<></>
                                                    
                                                }
                                                    <div className="flex items-center gap-2">
                                                        <University className="w-4 h-4 text-purple-500" />
                                                        <span>Capacity: 10/{event.capacity}</span>
                                                    </div>
                                                    <div className="flex items-center gap-2">
                                                        <MapPin className="w-4 h-4 text-purple-500" />
                                                        <span>{event.location}</span>
                                                    </div>
                                                </div>

                                                {/* الأسعار */}
                                                {event.prices && event.prices.length > 0 && (
                                                    <div className="flex flex-wrap gap-2 mt-4">
                                                        {event.prices.map((price, i) => (
                                                            <span
                                                                key={i}
                                                                className="bg-purple-50 text-purple-800 font-medium px-3 py-1 rounded-full text-sm shadow-sm"
                                                            >
                                                                {price.toFixed(2)} JOD
                                                            </span>
                                                        ))}
                                                    </div>
                                                )}
                                            </div>
                                        </EventCard>
                                </Link>
                                <motion.div
                                    initial={{ scale: 0 }}
                                    animate={{ scale: 1 }}
                                    className="absolute top-2 right-2 p-2 bg-purple-600/80 rounded-full
                                    backdrop-blur-sm"
                                >
                                    <Rocket size={18} className="text-cyan-400" />
                                </motion.div>
                            </motion.div>
                        ))}
                    </AnimatePresence>
                </motion.div>

                {/* Pagination Controls */}
                {
                    filteredEvents.length > eventsPerPage && (
                        <div className="flex justify-center mt-12">
                            <nav className="flex items-center gap-2">
                                <button
                                    onClick={() => paginate(Math.max(1, currentPage - 1))}
                                    disabled={currentPage === 1}
                                    className={`p-2 rounded-full ${currentPage === 1 ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'}`}
                                >
                                    <ChevronLeft className="text-purple-400" />
                                </button>

                                {Array.from({ length: totalPages }).map((_, index) => (
                                    <button
                                        key={index}
                                        onClick={() => paginate(index + 1)}
                                        className={`w-10 h-10 rounded-full flex items-center justify-center
                                                ${currentPage === index + 1
                                                ? 'bg-purple-600 text-white'
                                                : 'text-purple-400 hover:bg-purple-500/20'
                                            } `}
                                    >
                                        {index + 1}
                                    </button>
                                ))
                                }

                                <button
                                    onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
                                    disabled={currentPage === totalPages}
                                    className={`p-2 rounded-full ${currentPage === totalPages ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'} `}
                                >
                                    <ChevronRight className="text-purple-400" />
                                </button >
                            </nav >
                        </div >
                    )}
            </div >

            {/* Decorations */}
            < motion.div
                className="fixed top-0 left-0 w-full h-full pointer-events-none z-0"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
            >
                {
                    [...Array(8)].map((_, i) => (
                        <motion.div
                            key={i}
                            animate={{
                                x: [0, 100, 0],
                                y: [0, 50, 0],
                                rotate: [0, 180, 360]
                            }}
                            transition={{
                                duration: 12 + i * 2,
                                repeat: Infinity,
                                ease: "easeInOut"
                            }}
                            className="absolute text-purple-400/20"
                            style={{
                                left: `${Math.random() * 100}%`,
                                top: `${Math.random() * 100} %`
                            }}
                        >
                            <Stars size={48} />
                        </motion.div >
                    ))}
            </motion.div >
        </main >
    );
}
export default AllEventsPage;